@import "src/_include-media";
@import "src/_flex";
@import "src/_mixin-transition";

section.main-chart{
    display:none;
    height:auto;
    & .main-chart-container{
        // @include flexbox;
        // @include flex-direction(row);
        // @include justify-content(space-around);
        padding:3px;
        %mainChartContianer{
            background-color: unquote("rgba(#{$palette-cyan-300},0.1)");
            margin-left:auto;
            margin-right:auto;
        }
        & .main-chart__m-container--line{
            // background-color: rgba(178, 223, 219, 0.22);
            @extend %mainChartContianer;
            width:300px;
            height:150px;
        }
         & .main-chart__m-container--doughnut{
            // background-color: rgba(178, 223, 219, 0.22);
            @extend %mainChartContianer;
            width:180px;
            height:150px;
        }
    }
}